.LoginButtonFacebook{
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;

  .login{
    border-radius: 4px;
    box-shadow: none;
    font-size: 16px;
    font-weight: 400;
    margin: 5px 0;
    padding: 0.60rem;
    text-align: center;
    text-shadow: none;
    cursor: pointer;
    width: 100%;
    color: #fff !important;
    background: #3b5998;
    border-color: #3b5998;
  }

  .login:hover{
    color: #fff;
  }
}

