.input{
  margin-top: 4px;
  width: 100%;
  text-align: left;
  font-size: 16px;
  padding: 6px;
  border-top: 0;
  border-left: 0;
  border-right: 0;
  border-bottom: 1px solid #78909C;
  background: #fff !important;
}

.input:focus{
  outline: none;
  border-left: 0;
  border-right: 0;
  border-bottom: 1px solid $fouthcolor;
}

.input-error{
  margin-top: 4px;
  width: 100%;
  text-align: left;
  font-size: 16px;
  padding: 6px;
  border-radius: 5px;
  color: #ff5e36;
  border: 1px solid #ff5e36;           
}

.input-error:focus{
  outline: none;
  border: 1px solid #ff5e36;
}

.label-error{
  color: #ff5e36;
  border: 2px solid #ff5e36; 
  border-radius: 5px;
  display: block;
  padding: 2px 5px;
  font-weight: 700;
  width: 100%;
  margin-top: 10px;
  text-align: center;

}

.text-danger{
  color: #ff5e36;
}