.modal-width {
  width: 100%;
  margin: auto;
  max-width: 500px;
  height: 500px;
  display: block;
  overflow-y: scroll;
  scroll-behavior: smooth;
}

:focus {
  outline: none !important;
}

.modal-contact {
  max-width: 450px;
  width: 100%;

  textarea.form-control {
    height: 200px;
    border: 1px solid #78909c;
  }

  .form-control:focus {
    color: #495057;
    background-color: #fff;
    border-color: #12442e;
    outline: 0;
    box-shadow: none;
  }
  .payment-info {
    p {
      font-size: 18px;
      font-weight: 400;
    }

    span {
      font-size: 22px;
      font-weight: 600;
    }
  }

  .payment-notes {
    font-size: 14px;
    text-align: justify;
  }
}

.list-ul{
  list-style: circle !important;
}

.yellow{
  background-color: rgb(251, 255, 28);
}

.abc-lower{
  list-style-type:lower-alpha;
}

.roman-style{
  list-style-type:upper-roman;
}